.chatbotContainer {
  width: 100%;
  min-width: 30%;
  border-radius: 5px;
  bottom: 40px;
  box-shadow: 5px 5px 13px rgb(91 81 81 / 40%);
  display: flex;
  justify-content: center;
  margin: 10px 0;
  position: fixed;
  right: 40px;
  z-index: 6;
}

.react-chatbot-kit-chat-container {
  width: 100%;
    position: relative;
    bottom: 6%;
    right: 0%;
}

.chat-input-container {
  bottom: 0;
  display: flex;
  position: absolute;
  width: 100%;
}

.react-chatbot-kit-chat-inner-container {
  background-color: #fff;
  border-radius: 5px;
  height: 500px;
  margin-bottom: 0;
  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem;
}
.react-chatbot-kit-chat-header {
  align-items: center;
  background-color: #efefef;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  color: #514f4f;
  display: flex;
  font-family: Arial;
  font-size: .85rem;
  font-weight: 700;
  padding: 12.5px;
}

.react-chatbot-kit-chat-message-container {
  height: 424px;
  overflow: scroll;
  padding: 0 17.5px 10px;

  &:last-child {
    padding-bottom: 2.5rem;
}
}

.react-chatbot-kit-chat-input {
  border: none;
  border-bottom-left-radius: 5px;
  border-top: 1px solid #d8d8d8;
  font-size: .85rem;
  padding: 12.5px;
  width: 100%;
}
.react-chatbot-kit-chat-bot-message {
  margin-left: 0;
  width: 60%;
}


#button-overlay {
  position: fixed;
  display: none;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0,0,0,0.5);
  z-index: 2;
}

.chatbotHeaderAppBar {
  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem;
}